
.block-live-room-top {
  position: absolute;
  top: .5rem;
  width: 100%;
  z-index: 9;
}
.live-room {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.block-live-room-player {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9;
}
.block-live-room-player .cover {
  width: 100%;
  height: 100%;
}
.block-live-room-interact {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 9;
}
.block-live-room-message {
  position: absolute;
  bottom: 2.575rem;
  left: 0;
  z-index: 9;
  width: 100%;
  display: none;
}
.block-live-room-form {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 9;
}



