.svg-icon {
  width: 1rem;
  height: 1rem;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: baseline;
}
.mint-indicator-wrapper {
  z-index: 999;
}
/* 暂无数据 */
.null-data {
  width: 100%;
  text-align: center;
  padding: 1.5rem 0.75rem;
  color: #b8c0cc;
  background-color: #fafafa;
}
.null-data.hide {
  display: none;
}
.null-data span {
  margin-top: 0.5rem;
  display: block;
}
/* 无更多数据 */
.no-more-data {
  text-align: center;
  color: #999;
  margin: .5rem 0;
  font-size: .65rem;
}
.mint-swipe-indicator {
  width: .25rem;
  height: .25rem;
  border-radius: .25rem;
}
.mint-swipe-indicator.is-active {
  width: 1rem;
}
.sex-man {
  color: #4a8ec4;
}
.sex-woman {
  color: #d86d9e;
}
.icon-level {
  height: .7rem;
}
.developing {
  position: absolute;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  padding: 1rem .5rem;
}
.developing:before {
  content: '\e64d';
  font-family: 'iconfont';
  font-size: 2rem;
}
body,
html {
  position: relative;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
.view,
.views {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1000;
}
.views {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.view {
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.pages {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #efeff4;
}
.page {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #efeff4;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.page-content {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%;
  position: relative;
  z-index: 1;
}
.header-bar ~ .page-content {
  padding-top: 2.25rem;
}
.footer-bar ~ .page-content {
  padding-bottom: 2.5rem;
}
.swiper-container-gift .swiper-pagination-gift {
  position: absolute;
  bottom: .35rem;
  left: 0;
  z-index: 9999;
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.swiper-container-gift .swiper-pagination-gift .swiper-pagination-bullet {
  background: #fff;
  width: .2rem;
  height: .2rem;
  opacity: 1;
  border-radius: .2rem;
  display: block;
}
.swiper-container-gift .swiper-pagination-gift .swiper-pagination-bullet.swiper-pagination-bullet-active {
  width: .3rem;
  height: .3rem;
}
.swiper-pagination-subType {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 2rem;
  line-height: 2rem;
  border-bottom: .05rem solid #e6e8f1;
  position: fixed;
  background: #fff;
  top: 4.4rem;
  z-index: 999;
  width: 100%;
}
.swiper-pagination-subType .swiper-pagination-bullet {
  width: initial;
  opacity: 1;
  background: transparent;
}
.swiper-pagination-subType .swiper-pagination-bullet .item-title {
  color: #999;
  padding: 0 8px .5rem 8px;
}
.swiper-pagination-subType .swiper-pagination-bullet-active .item-title {
  border-bottom: 2px solid #333;
  color: #333;
  padding: 0 8px .5rem 8px;
}
.video-player-box {
  width: 100%;
  height: 100%;
}
.video-player-box .vjs-poster {
  background-size: cover;
}
.video-player-box .video-js {
  width: 100%;
  height: 100%;
