}
.svideo-player .video-js {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.svideo-player .video-js .vjs-tech {
  position: unset;
}
.svideo-player .video-js .vjs-big-play-button {
  position: absolute;
