.live-category {
  position: fixed;
  top: 2.25rem;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 1.75rem;
  line-height: 1.75rem;
  background-color: #fff;
  overflow: hidden;
}
.live-category .live-category-item {
  color: #999;
  font-size: .6rem;
  padding: 0 10px;
  line-height: 100%;
}
.live-category .swiper-pagination-category {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  min-width: 100%;
}
.live-category .swiper-pagination-category .live-category-item {
  height: 1.75rem;
  line-height: 1.75rem;
  float: left;
}
.live-category .swiper-pagination-bullet {
  width: initial;
  height: initial;
  background: none;
  opacity: 1;
  border-radius: 0;
}
.live-category .swiper-pagination-bullet-active .item-title {
  border-bottom: 2px solid #333;
  color: #333;
  padding-bottom: 9px;
}
.live-main {
  padding-top: 1.75rem;
}



